@import 'src/sass/variables';
#lightbox {
    position: fixed;
    z-index: 9999;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.8);
}

#lightbox .close {
    position: absolute;
    top: 20px;
    right: 35px;
    color: white;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
    cursor: pointer;
}

#lightbox .lightbox-content {
    display: block;
    margin: auto;
    max-width: 80%;
    max-height: 80%;
    margin-top: 60px;
}

#lightbox .caption {
    margin: auto;
    display: block;
    text-align: center;
    color: white;
    padding: 10px 20px;
    height: 150px;
}
.wp-block-cab-image-gallery{
    .gallery-title-container {
        padding-top: 50px;
        .gallery-title{
            font-family: $fontFamilyAdditional;
            font-weight: 400;
            font-size: $font60;
            color:$darkGreen;
        }
    }

    .container-gallery-fullwidth{
        max-width: 100vw !important;

        .gallery-row:first-child {
            border-left: 6px solid #fff;
        }
        .image-gallery-single-img{
            margin:0px;
            padding: 0px;
            border: solid #fff;
            border-width: 6px 6px 6px 0px;

        }
    }
}

